.RewardPage {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
.RewardPage__header {
  font-size: 28px;
  width: 80%;
  padding: 50px;
}
.RewardPage__score {
  color: red;
  font-size: 32px;
}
.RewardPage__balance {
  font-size: 28px;
  width: 80%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.RewardPage__rewards {
  padding: 30px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}
.RewardPage__get-prises {
  width: 150px;
  height: 150px;
  color: #ffffff;
  font-size: 28px;
  padding-bottom: 20px;
  background: -webkit-gradient(linear, left top, right top, from(#857a5f), to(#f0eddb));
  background: -webkit-linear-gradient(left, #857a5f, #f0eddb);
  background: linear-gradient(90deg, #857a5f, #f0eddb);
  opacity: 0.8;
  -webkit-transition: all 300ms;
  transition: all 300ms;
  border: 0;
  border-radius: 50%;
}
.RewardPage__get-prises_active {
  background: -webkit-gradient(linear, left top, right top, from(#85610f), to(#f0a123));
  background: -webkit-linear-gradient(left, #85610f, #f0a123);
  background: linear-gradient(90deg, #85610f, #f0a123);
}
.RewardPage__get-prises:hover {
  background: -webkit-gradient(linear, left top, right top, from(#85610f), to(#f0a123));
  background: -webkit-linear-gradient(left, #85610f, #f0a123);
  background: linear-gradient(90deg, #85610f, #f0a123);
  opacity: 1;
}
.RewardPage__notification {
  opacity: 0;
  width: 600px;
  height: 100px;
  font-size: 24px;
  color: #d9e1fd;
  background-color: #970018;
  -webkit-transition: all 300ms;
  transition: all 300ms;
  position: absolute;
  top: -100px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  z-index: 10;
  padding: 10px;
}
.RewardPage__notification_show {
  opacity: 1;
  -webkit-transform: translate(-50%, 300px);
          transform: translate(-50%, 300px);
}
