.WelcomePage {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.WelcomePage h1 {
  font-size: 32px;
}
.WelcomePage__container {
  height: 40%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  padding: 0 30px;
}
.WelcomePage__container > h1 {
  font-weight: bold;
  text-transform: uppercase;
  padding: 0 30px 30px 30px;
  color: #464646;
  text-align: center;
}
.WelcomePage__container > button {
  width: 30%;
  min-width: 200px;
  max-width: 400px;
  height: 70px;
  font-size: 24px;
  background: #55b45a;
  border-radius: 20px;
  border: 0;
  box-shadow: 0 0 5px black;
  background: -webkit-gradient(linear, left top, left bottom, from(#077414), to(#87f470));
  color: white;
}
.WelcomePage__container > button:focus {
  outline: 0;
  box-shadow: 0 0 10px black;
}
