.EndGamePage {
  width: 100%;
  height: 100%;
  background: url(/birthday-test/static/media/petal.8dc922e1.png);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.EndGamePage__header {
  font-size: 36px;
  font-weight: bold;
  width: 70%;
  color: #8b571c;
  padding: 30px;
  text-align: center;
}
.EndGamePage__prises-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.EndGamePage__prise {
  font-weight: bold;
  color: #113e13;
  font-size: 48px;
  margin-bottom: 20px;
}
.EndGamePage__prise:last-child {
  margin-bottom: 0;
}
