.AnswerOption {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 50px;
}
.AnswerOption:last-child {
  margin-bottom: 0;
}
.AnswerOption__button {
  height: 40px;
  background: -webkit-gradient(linear, left top, right top, from(#0a1050), to(#3c1261));
  background: -webkit-linear-gradient(left, #0a1050, #3c1261);
  background: linear-gradient(90deg, #0a1050, #3c1261);
  border: 0;
  border-radius: 50px;
  min-width: 500px;
  color: #d9e1fd;
  font-size: 18px;
  box-shadow: 0 0 5px 5px #ffffff;
  -webkit-transition: box-shadow 0.3s ease, background-color 500ms;
  transition: box-shadow 0.3s ease, background-color 500ms;
}
.AnswerOption__button:focus {
  outline: 0;
}
.AnswerOption__button:active {
  box-shadow: 0 0 2px #ffffff;
}
.AnswerOption__button:hover {
  background: -webkit-gradient(linear, left top, right top, from(#0c1585), to(#661799));
  background: -webkit-linear-gradient(left, #0c1585, #661799);
  background: linear-gradient(90deg, #0c1585, #661799);
}
.AnswerOption__button_chosen {
  -webkit-animation: flash 0.6s infinite;
          animation: flash 0.6s infinite;
}
@keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}
