@-webkit-keyframes smoothly-appearance {
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}
@keyframes smoothly-appearance {
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}
.blink {
  -webkit-animation: flash 0.6s infinite;
          animation: flash 0.6s infinite;
}
@-webkit-keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}
@keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}
.Question {
  opacity: 0;
  width: 100%;
  -webkit-animation: smoothly-appearance 1.5s forwards;
          animation: smoothly-appearance 1.5s forwards;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.Question__question-text {
  position: absolute;
  padding: 10px;
  top: 50px;
  color: #191932;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 28px;
  -webkit-animation: smoothly-appearance 1.5s forwards;
          animation: smoothly-appearance 1.5s forwards;
}
.Question__answers {
  position: absolute;
  top: 450px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
